import React from 'react'
import { ResponsivePie } from '@nivo/pie'
import { Spin } from 'antd';
// make sure parent container have a defined height when using
// responsive component, otherwise height will be 0 and
// no chart will be rendered.
// website examples showcase many properties,
// you'll often use just a few of them.



const MyPie = (props) => {
    const data = [
        {
            "id": "Profesor",
            "label": "Profesor",
            "value": props.api ? props.data.dos : null,
            "color": "hsl(170, 52%, 56%)"
        },
        {

            "id": "Estudiante",
            "label": "Estudiante",
            "value": props.api ? props.data.uno : null,
            "color": "hsl(279, 70%, 50%)"
        },
        {
            "id": "Administrativo",
            "label": "Administrativo",
            "value": props.api ? props.data.tres : null,
            "color": "hsl(91, 70%, 50%)"
        }
    ]
    return (
        <>
            {props.api ?
                <ResponsivePie
                    data={data}
                    margin={{ top: 40, right: 80, bottom: 80, left: 80 }}
                    innerRadius={0.5}
                    padAngle={0.7}
                    cornerRadius={3}
                    colors={{ scheme: 'nivo' }}
                    borderWidth={1}
                    borderColor={{ from: 'color', modifiers: [['darker', 0.2]] }}
                    radialLabelsSkipAngle={10}
                    radialLabelsTextXOffset={3}
                    radialLabelsTextColor="#333333"
                    radialLabelsLinkOffset={0}
                    radialLabelsLinkDiagonalLength={16}
                    radialLabelsLinkHorizontalLength={24}
                    radialLabelsLinkStrokeWidth={1}
                    radialLabelsLinkColor={{ from: 'color' }}
                    slicesLabelsSkipAngle={10}
                    slicesLabelsTextColor="#333333"
                    animate={true}
                    motionStiffness={90}
                    motionDamping={15}
                    defs={[
                        {
                            id: 'dots',
                            type: 'patternDots',
                            background: 'inherit',
                            color: 'rgba(255, 255, 255, 0.3)',
                            size: 4,
                            padding: 1,
                            stagger: true
                        },
                        {
                            id: 'lines',
                            type: 'patternLines',
                            background: 'inherit',
                            color: 'rgba(255, 255, 255, 0.3)',
                            rotation: -45,
                            lineWidth: 6,
                            spacing: 10
                        }
                    ]}
                    fill={[
                        {
                            match: {
                                id: 'Estudiante'
                            },
                            id: 'dots'
                        },
                        {
                            match: {
                                id: 'Profesor'
                            },
                            id: 'dots'
                        },
                        {
                            match: {
                                id: 'Administrativo'
                            },
                            id: 'lines'
                        }
                    ]}
                    legends={[
                        {
                            anchor: 'bottom',
                            direction: 'row',
                            translateY: 56,
                            itemWidth: 100,
                            itemHeight: 18,
                            itemTextColor: '#999',
                            symbolSize: 18,
                            symbolShape: 'circle',
                            effects: [
                                {
                                    on: 'hover',
                                    style: {
                                        itemTextColor: '#000'
                                    }
                                }
                            ]
                        }
                    ]}
                />
                :  <Spin size="large" />}
        </>
    )
}

export default MyPie;