import React from 'react'
import { ResponsiveBar } from '@nivo/bar'
import { Spin } from 'antd';
// make sure parent container have a defined height when using
// responsive component, otherwise height will be 0 and
// no chart will be rendered.
// website examples showcase many properties,
// you'll often use just a few of them.
const MyBar = (props) => {
    const data = [
        {
          "country": "Calidad de los insumos",
          "Deficiente": props.api ? props.insumos.deficiente: null,
          "DeficienteColor": "hsl(82, 70%, 50%)",
          "Malo": props.api ? props.insumos.malo: null,
          "MaloColor": "hsl(222, 70%, 50%)",
          "Regular": props.api ? props.insumos.regular: null,
          "RegularColor": "hsl(70, 70%, 50%)",
          "Bueno": props.api ? props.insumos.bueno: null,
          "BuenoColor": "hsl(85, 70%, 50%)",
          "Excelente": props.api ? props.insumos.excelente: null,
          "ExcelenteColor": "hsl(24, 70%, 50%)",
        },
        {
          "country": "Mecanismos de comunicacion",
          "Deficiente": props.api ? props.comunicacion.deficiente: null,
          "DeficienteColor": "hsl(254, 70%, 50%)",
          "Malo": props.api ? props.comunicacion.malo: null,
          "MaloColor": "hsl(10, 70%, 50%)",
          "Regular": props.api ? props.comunicacion.regular: null,
          "RegularColor": "hsl(33, 70%, 50%)",
          "Bueno": props.api ? props.comunicacion.bueno: null,
          "BuenoColor": "hsl(32, 70%, 50%)",
          "Excelente": props.api ? props.comunicacion.excelente: null,
          "ExcelenteColor": "hsl(27, 70%, 50%)",
        },
        {
          "country": "Apoyo de rectoria",
          "Deficiente":  props.api ? props.apoyo.deficiente: null,
          "DeficienteColor": "hsl(207, 70%, 50%)",
          "Malo": props.api ? props.apoyo.malo: null,
          "sandMaloColorwichColor": "hsl(115, 70%, 50%)",
          "Regular": props.api ? props.apoyo.regular: null,
          "RegularColor": "hsl(22, 70%, 50%)",
          "Bueno": props.api ? props.apoyo.bueno: null,
          "BuenoColor": "hsl(266, 70%, 50%)",
          "Excelente": props.api ? props.apoyo.excelente: null,
          "donutExcelenteColorColor": "hsl(347, 70%, 50%)",
        }
      ]
    return (
    <>
    {props.api ?
    <ResponsiveBar
        data={data}
        keys={[ 'Excelente' ,'Deficiente', 'Malo', 'Regular', 'Bueno']}
        indexBy="country"
        margin={{ top: 50, right: 130, bottom: 50, left: 60 }}
        padding={0.3}
        colors={['#61CDBB', '#F5EA8C']}
        defs={[
            {
                id: 'dots',
                type: 'patternDots',
                background: 'inherit',
                color: '#38bcb2',
                size: 4,
                padding: 1,
                stagger: true
            },
            {
                id: 'lines',
                type: 'patternLines',
                background: 'inherit',
                color: '#eed312',
                rotation: -45,
                lineWidth: 6,
                spacing: 10
            }
        ]}
        fill={[
            {
                match: {
                    id: 'Excelente'
                },
                id: 'dots'
            },
            {
                match: {
                    id: 'Bueno'
                },
                id: 'lines'
            }
        ]}
        borderColor={{ from: 'color', modifiers: [ [ 'darker', 1.6 ] ] }}
        axisTop={null}
        axisRight={null}
        axisBottom={{
            tickSize: 5,
            tickPadding: -5,
            tickRotation: 0,
            legend: 'Servicio',
            legendPosition: 'middle',
            legendOffset: 32
        }}
        axisLeft={{
            tickSize: 5,
            tickPadding: 5,
            tickRotation: 0,
            legend: 'Calificación',
            legendPosition: 'middle',
            legendOffset: -40
        }}
        labelSkipWidth={12}
        labelSkipHeight={12}
        labelTextColor={{ from: 'color', modifiers: [ [ 'darker', 1.6 ] ] }}
        legends={[
            {
                dataFrom: 'keys',
                anchor: 'bottom-right',
                direction: 'column',
                justify: false,
                translateX: 120,
                translateY: 0,
                itemsSpacing: 2,
                itemWidth: 100,
                itemHeight: 20,
                itemDirection: 'left-to-right',
                itemOpacity: 0.85,
                symbolSize: 20,
                effects: [
                    {
                        on: 'hover',
                        style: {
                            itemOpacity: 1
                        }
                    }
                ]
            }
        ]}
        animate={true}
        motionStiffness={90}
        motionDamping={15}
    />
    :  <Spin size="large" />}
    </>
)}

export default MyBar;