import React from 'react'
import { ResponsiveRadar } from '@nivo/radar'
// make sure parent container have a defined height when using
// responsive component, otherwise height will be 0 and
// no chart will be rendered.
// website examples showcase many properties,
// you'll often use just a few of them.
const MyRadar = (props) => {
    const data = [
        {
          "taste": "Excelente",
          "Usuarios": props.api ? props.data.excelente : null
        },
        {
          "taste": "Bueno",
          "Usuarios": props.api ? props.data.bueno : null
        },
        {
          "taste": "Regular",
          "Usuarios": props.api ? props.data.regular : null
        },
        {
          "taste": "Malo",
          "Usuarios": props.api ? props.data.malo : null
        },
        {
          "taste": "Deficiente",
          "Usuarios": props.api ? props.data.deficiente : null
        }
      ]
      
    return (
      <>
      {props.api ? 
    <ResponsiveRadar
        data={data}
        keys={[ 'Usuarios' ]}
        indexBy="taste"
        maxValue="auto"
        margin={{ top: 70, right: 80, bottom: 40, left: 80 }}
        curve="linearClosed"
        borderWidth={2}
        borderColor={{ from: 'color' }}
        gridLevels={5}
        gridShape="circular"
        gridLabelOffset={36}
        enableDots={true}
        dotSize={10}
        dotColor={{ theme: 'background' }}
        dotBorderWidth={2}
        dotBorderColor={{ from: 'color' }}
        enableDotLabel={true}
        dotLabel="value"
        dotLabelYOffset={-12}
        colors={{ scheme: 'nivo' }}
        fillOpacity={0.25}
        blendMode="multiply"
        animate={true}
        motionStiffness={90}
        motionDamping={15}
        isInteractive={true}
        legends={[]}
    />
    : null}
    </>
)}

export default MyRadar;